import React, { Component } from 'react';
import {formatNumber} from './processor';

class Tooltip extends Component {

  render() {
    const {content, top, left} = this.props;
    const {sets, totalVal, onlyVal, relVal} = content;
    const labels = {
      fb: 'LI',
      ig: 'EG',
      wa: 'FR',
    }
    const comboLabel = sets.map(set => labels[set]).join(' & ').toUpperCase();

    return (<div className="tooltip" style={{top, left}}>
        <div>
          {onlyVal != null ? 'Total ' : ''}<strong>{comboLabel}</strong>
          <br/>
          {formatNumber(totalVal)}
          {relVal != null ? ` (${(totalVal / relVal * 100).toFixed(1)}%)` : ''}
        </div>
        {onlyVal != null && <div>
          Only <strong>{comboLabel}</strong>
          <br/>
          {formatNumber(onlyVal)}
          {relVal != null ? ` (${(onlyVal / relVal * 100).toFixed(1)}%)` : ''}
        </div>}
      </div>);
  }
}

export default Tooltip;
