import React, { Component } from 'react';
import ReactCountryFlag from 'react-country-flag';
import CirclesStatic from './CirclesStatic';
import CirclesAnimated from './CirclesAnimated';
import {
  worldData,
  allMonths,
  formatMonth,
  getSortCountriesOptions,
  getResizeCirclesOptions,
  getOrderCirclesOptions,
  getShowLabelsOptions,
  orderCountries,
  getLargestCountryInfo,
  formatNumber,
  reverseList,
} from './processor';
import {getVennDiagram,  getVennDiagramsAnimated} from './vennDiagram';
import './VisualizationControl.css';
import './Circles.css';

class VisualizationControl extends Component {

  constructor(props) {
    super(props);
    // visuals change state
    this.state = {
      // sorting countries
      orderBy: 'total',
      orderDir: 'desc',
      // sizing circles
      sizeBase: 'history' ,
      isSizeBaseOn: true,
      isCountryBaseOn: true,
      // order of circles in a country
      circleOrder: 'map',
      // show labels in a country
      label: 'total-topline',
      labelType: 'number',
      // array of selected countries from the parent component are mutated by sorting
      countries: props.countries,
    };
  }

  // coutry diagram size
  WRAPPER_SIZE = 300;

  toggleSettingPanel(stateName) {
    this.setState(currentState => {
      const currentValue = currentState[stateName];
      return {[stateName]: !currentValue};
    });
  }

  setAbsoluteSizeBase(sizeBase) {
    this.props.animationVoid(sizeBase.split('-')[1] === 'abs');
    this.setState({sizeBase});
  }

  orderCountries(value) {
    this.setState(currentState => {
      return {
        orderBy: value,
        countries: orderCountries(
          currentState.countries,
          this.props.month,
          value,
          currentState.orderDir
        )};
    });
  }

  reverseCountries(value) {
    this.setState(currentState => {
      return {
        orderDir: value,
        countries: reverseList(currentState.countries)};
    });
  }

  static getDerivedStateFromProps(props, state) {
    if (props.countries !== state.countries) {
      const {countries, month} = props;
      const {orderBy, orderDir} = state;
      return {countries: orderCountries(countries, month, orderBy, orderDir)};
    }
    return null;
  }

  render() {
    const {showWorld, apps, month, isAnimationStarted, animatedMonthIndex, isUpdating} = this.props;
    const {
      countries, orderBy, orderDir,
      sizeBase, isSizeBaseOn, isCountryBaseOn, circleOrder,
      label, labelType,
    } = this.state;

    const sortCountriesOptions = getSortCountriesOptions(apps)
    const resizeCirclesOptions = getResizeCirclesOptions(apps, countries.length)
    const orderCirclesOptions = getOrderCirclesOptions(apps)
    const showLabelsOptions = getShowLabelsOptions(apps)

    // for absolute sizing
    const sizePop = sizeBase.split('-')[0];
    const largestCountry = sizeBase.split('-')[1] === 'abs' && countries.length > 0 ?
      getLargestCountryInfo(countries, month, sizePop) :
      null;

    // add world at the beginning of countries lis
    const allVisList = showWorld ? [worldData].concat(countries) : countries;

    const SortCountries = (<div className="setting-vis-item">
        <div className="setting-vis-title">Sort countries</div>
        <div className="setting-select-wrapper">
          <select
            defaultValue={orderBy}
            onChange={(e) => this.orderCountries(e.target.value)}
            disabled={isAnimationStarted}
            >
            {sortCountriesOptions.map(d => <option
              key={d.value}
              value={d.value}
              >{d.label}</option>)}
          </select>
        </div>
        <div className="setting-select-wrapper">
          {[
             {value: 'desc', label: 'Large to small'},
             {value: 'asc', label: 'Small to large'},
           ].map(d => <span key={d.value} className="vis-option">
            <input
              type="radio"
              name="orderDir"
              value={d.value}
              checked={d.value === orderDir}
              onChange={(e) => this.reverseCountries(e.target.value)}
              disabled={isAnimationStarted}
            /> {d.label}
          </span>)}
        </div>
      </div>);

    const ResizeCircles = (<div>
        <div className="setting-vis-title">
          Resize circles based on
        </div>
        <div className="setting-select-wrapper">
          <select
            defaultValue={sizeBase}
            onChange={(e) => this.setAbsoluteSizeBase(e.target.value)}
            disabled={isAnimationStarted}
            >
            {resizeCirclesOptions.map(d => <option
              key={d.value}
              value={d.value}
            >{d.label}</option>)}
          </select>
        </div>
        <div className="setting-select-wrapper">
          <div className="vis-option">
            <input
              type="checkbox"
              name="isSizeBaseOn"
              checked={isSizeBaseOn}
              onChange={(e) => this.toggleSettingPanel('isSizeBaseOn', isSizeBaseOn)}
              disabled={isAnimationStarted}
            /> Show base circle {largestCountry != null &&
              `(${largestCountry.name}: ${formatNumber(largestCountry.value)}
              ${largestCountry.isCalibrated ? ', calibrated' : ''})`}
          </div>
          {(largestCountry != null || sizeBase === 'history') && <div className="vis-option">
            <input
              type="checkbox"
              name="isCountryBaseOn"
              checked={isCountryBaseOn}
              onChange={(e) => this.toggleSettingPanel(
                'isCountryBaseOn', isCountryBaseOn
              )}
              disabled={isAnimationStarted}
            /> Show {sizeBase === 'history' ?
              `MAP of ${formatMonth(month)}` :
              `${sizePop === 'internet' ?
                  'internet population' :
                  'total population'} of each country`}
          </div>}
          {!(sizeBase === 'fit' || sizeBase === 'history') && <div className="warning setting-warning">
              Internet Population (iPoP) and total population are only an estimation. {` `}
              Thus, in some countries total family MAP can be larger than iPop.{` `}
              Please be aware of this limitation as you use this tool and gain insights.
          </div>}
        </div>
      </div>);

    const OrderCircles = (<div className="second-title">
        <div className="setting-vis-title">
          Order circles in a country
        </div>
        <div className="setting-select-wrapper">
          <select
            defaultValue={circleOrder}
            onChange={(e) => this.setState({circleOrder: e.target.value})}
            disabled={isAnimationStarted}
            >
            {orderCirclesOptions.map(d => <option
              key={d.value}
              value={d.value}
            >{d.label}</option>)}
          </select>
        </div>
      </div>);

    const labelLevel = label.split('-')[0];
    const labelValue = label.split('-')[1];

    const ShowLabels = (<div className="setting-vis-item">
        <div className="setting-vis-title">
          Show labels
        </div>
        <div className="setting-select-wrapper">
          <select
            defaultValue={label}
            onChange={(e) => this.setState({label: e.target.value})}
            disabled={isAnimationStarted}
            >
            {showLabelsOptions.map(d => <option
              key={d.value}
              value={d.value}
            >{d.label}</option>)}
          </select>
        </div>
        {label !== 'none' && <div className="setting-select-wrapper">
          {[
             {value: 'number', label: 'MAP'},
             {value: 'percentage', label: 'MAP / Internet Population (%'},
           ].map(d => <div key={d.value} className="vis-option">
            <input
              type="radio"
              name="labelType"
              value={d.value}
              checked={d.value === labelType}
              onChange={(e) => this.setState({labelType: e.target.value})}
              disabled={isAnimationStarted}
            /> {d.label}{d.value === 'percentage' && labelValue === 'growth' ? 'p' : ''}
            {d.value === 'percentage' && labelValue === 'topline' ? ', not calibrated' : ''}
            {d.value === 'percentage' ? ')' : ''}
          </div>)}
        </div>}
      </div>);

    return (<div>
      {allVisList.length > 0 && <div className={`setting-vis${isAnimationStarted ? ' disabled' : ''}`}>
        {SortCountries}
        <div className="setting-vis-item">
          {ResizeCircles}
          {apps.length > 1 && OrderCircles}
        </div>
        {ShowLabels}
        {(isAnimationStarted && animatedMonthIndex >= 0) &&
          <div className="setting-vis-month">
            {allMonths[animatedMonthIndex].label}
          </div>}
      </div>}
      <div className="small-multiples">
        {isUpdating ? <div className="updating">Updating...</div> :
        <div className="all-countries">
          {allVisList.map((c, i) => {
            const id = c.country;
            const absValInfo = id !== 'world' ? largestCountry : null;
            return (<div key={id} className="country-wrapper" style={{width: this.WRAPPER_SIZE}}>
              <div className="country-info">
                {!(showWorld && i === 0) && <span className="ranking">{i + (showWorld ? 0 : 1)}. </span>}
                <span className="name">{c.name}</span>
                {id !== 'world' && <ReactCountryFlag code={id} svg/>}
              </div>
              {isAnimationStarted ?
                <CirclesAnimated
                  country={id}
                  wrapperSize={this.WRAPPER_SIZE}
                  isSizeBaseOn={isSizeBaseOn}
                  labelLevel={labelLevel}
                  labelValue={labelValue}
                  labelType={labelType}
                  animationData={getVennDiagramsAnimated(
                    apps, c, sizeBase, circleOrder,
                    this.WRAPPER_SIZE, absValInfo,
                  )}
                  monthIndex={animatedMonthIndex}
                /> :
                <CirclesStatic
                  country={id}
                  isSizeBaseOn={isSizeBaseOn}
                  isCountryBaseOn={isCountryBaseOn}
                  labelLevel={labelLevel}
                  labelValue={labelValue}
                  labelType={labelType}
                  wrapperSize={this.WRAPPER_SIZE}
                  {...getVennDiagram(
                    apps, month, c,
                    sizeBase, circleOrder,
                    this.WRAPPER_SIZE,
                    absValInfo
                  )}
                />}
              </div>)
          })}
        </div>}
      </div>
    </div>);
  }
}

export default VisualizationControl;
