import {
  getAppCombos,
  getFamilyMAP,
  formatNumber,
  formatMonth,
  getPercentNumber,
  allMonths,
} from './processor';

import * as venn from 'venn.js';

const getSizeBaseInfo = (sizeBase) => {
  const basePop = sizeBase.split('-')[0];
  const baseText = basePop === 'internet' ? 'Internet population' : 'Total population';
  const baseType = sizeBase.split('-')[1];
  return {basePop, baseText, baseType};
};

const getHistoryUnion = (sizeBase, data, apps) => {
  return sizeBase === 'history' ?
    data.max_map[apps.join(':')] :
    {value: null, month: null};
};

const onlyMonths = allMonths.map(d => d.value);

const getToplineLabel = (familyMAP, currentData) => {
  return {
    number: formatNumber(familyMAP),
    percentage: Math.round(familyMAP / currentData.internet * 1000) / 10 + '%',
  };
};

const getGrowthLabel = (appCombos, familyMAP, prevfamilyMAP, prevData, currentData) => {
  const diff = familyMAP - prevfamilyMAP;
  const diffP = familyMAP / currentData.internet
    - prevfamilyMAP / prevData.internet;
  let arrow = diff > 0 ? 'up' : 'down';
  let arrowP = diffP > 0 ? 'up' : 'down';
  return {
    number: formatNumber(Math.abs(diff)),
    percentage: Math.round(Math.abs(diffP) * 1000) / 10 + '%p',
    arrow: {number: arrow, percentage: arrowP},
  };
}

const getAppMAPLabels = (data, month, order, appCircles, appCombos) => {
  const prevMonthIdx = onlyMonths.indexOf(month) - 1;
  const currentData = data[month];
  const prevData = prevMonthIdx > -1 ? data[onlyMonths[prevMonthIdx]] : null;

  const familyMAP = getFamilyMAP(appCombos, currentData);
  const totalLabel = {topline: getToplineLabel(familyMAP, currentData), growth: null};

  if (prevData != null) {
    const prevfamilyMAP = getFamilyMAP(appCombos, prevData);
    totalLabel.growth = getGrowthLabel(appCombos, familyMAP, prevfamilyMAP,
      prevData, currentData);
  }

  let appLabels = [];

  for (let app in order) {
    const {x, y, radius} = appCircles[app];
    const circleCount = Object.keys(appCircles).length;
    const index = order[app];
    const radian = Math.PI * 2 / circleCount * index -
      (circleCount === 2 ? (Math.PI / 2 - Math.PI / 6): 0);
    const posX = (x + radius * Math.sin(radian)).toFixed(1) + 'px';
    const posY = (y - radius * Math.cos(radian)).toFixed(1) + 'px';
    const label = {
      app,
      index,
      pos: {top: posY, left: posX},
      topline: getToplineLabel(currentData[app], currentData),
      growth: prevData != null ?
        getGrowthLabel(appCombos, currentData[app], prevData[app], prevData, currentData) :
        null,
    };
    appLabels.unshift(label);
  }

  return {totalLabel, appLabels};
};

const getBaseValue = (appCombos, monthData, historyUnionValue, basePop, data, sizeBase, absValInfo) => {

  // get the union value of the selected month MAP & selected apps
  const unionValue = getFamilyMAP(appCombos, monthData);

  // each country's non-calibrated value, not an absolute value for all countries
  let countryBaseValue = historyUnionValue;
  if (sizeBase === 'fit') {
    countryBaseValue = unionValue;
  } else if (sizeBase !== 'history'){
    countryBaseValue = monthData[basePop];
  }

  // get base value of each country - outer circle
  let baseValue = countryBaseValue;
  // calibrate baseValue if unionValue is bigger than baseValue
  if ((unionValue > baseValue && sizeBase !== 'history') ||
    (absValInfo != null && absValInfo.country === data.country)) {
    baseValue = unionValue;
  } else if (absValInfo != null) {
    baseValue = absValInfo.value;
  }
  const isCalibrated = unionValue > countryBaseValue;

  return {unionValue, countryBaseValue, baseValue, isCalibrated};
};

const getAppCircles = (unionValue, countryBaseValue, baseValue, isCalibrated, wrapper, appCombos, allMonthData, month, circleOrder) => {

  // get base circle radius and padding
  const boundingR = Math.sqrt(wrapper * wrapper -
    4 * (wrapper * wrapper / 4 - (wrapper * wrapper * unionValue) /
    (4 * baseValue)));
  const padding = Math.abs(wrapper - boundingR);

  // venn diagram data before drawing circles
  const sets = appCombos.map(combo => {
    return {sets: combo.split('-'), size: allMonthData[month][combo]};
  })
  let solution = venn.venn(sets, {lossFunction: venn.lossFunction});

  // change the order of the circles
  let orderFunc = circleOrder === 'map' ?
    // clockwise from the top or left (when two apps are selected)
    (a, b) => { return (a.radius - b.radius) * (appCombos.length === 3 ? -1 : 1)} :
    (a, b) => {
      const orders = {};
      const orderArr = circleOrder.split('-');
      for (let i in orderArr) {
        orders[orderArr[i]] = i;
      }
      return (orders[b.setid] - orders[a.setid]) * (appCombos.length === 3 ? -1 : 1);
    };
  solution = venn.normalizeSolution(solution, Math.PI / 2, orderFunc);

  // get size of circles of apps with width, height, and padding
  const appCircles = venn.scaleSolution(solution, wrapper, wrapper,
    Math.max(padding / 2, 1.5));

  // app-level labels
  let circleArr = [];
  for (let a in appCircles) {
    circleArr.push({setid: a, ...appCircles[a]});
  }
  const orders = circleArr.sort(orderFunc);
  if (circleArr.length !== 2) {
    orders.reverse();
  }
  const orderByApp = {}
  for (let o in orders) {
    orderByApp[orders[o].setid] = +o;
  }

  const {totalLabel, appLabels} = getAppMAPLabels(allMonthData, month, orderByApp, appCircles, appCombos);

  return {sets, appCircles, appLabels, totalLabel};
}

const getOutlineTooltips = (unionValue, countryBaseValue, historyUnionData, popValue, apps, month, sizeBase, country, absValInfo) => {
  const {baseText} = getSizeBaseInfo(sizeBase);

  let countryInfo = null;
  if (sizeBase === 'history') {
    countryInfo = {
      head: `Historically largest ${apps.length === 3 ? 'all family' : apps.join(' & ').toUpperCase()}`,
      value: formatNumber(countryBaseValue),
      month: formatMonth(historyUnionData.month),
      additional: unionValue >= countryBaseValue * 0.9 && historyUnionData.month !== month ?
        {head: `MAP in ${formatMonth(month)}`, value: formatNumber(unionValue)} :
        null,
    };
  } else if (sizeBase !== 'fit'){
    countryInfo = {
      head: `${baseText} in ${formatMonth(month)}`,
      value: formatNumber(popValue),
      calibratedTo: unionValue > countryBaseValue ?
        formatNumber(unionValue) :
        null,
      additional: unionValue <= countryBaseValue ?
        {
          head: 'Not on any apps',
          value: getPercentNumber(popValue - unionValue, popValue)
        } :
        null,
    };
  }

  const hasAbsInfo = absValInfo != null && country !== 'world';
  const absInfo = hasAbsInfo ?
    {
      head: `${baseText} in ${absValInfo.country !== country ? `${absValInfo.name}, ` : ''} ${formatMonth(month)}`,
      value: formatNumber(absValInfo.value),
      estimated : country === absValInfo.country && absValInfo.isCalibrated ?
        formatNumber(countryBaseValue) :
        null,
    } :
    null;

  const outerCircle = hasAbsInfo ? absInfo : countryInfo;

  // inner circle is diaplayed only the union value is smaller than the 90% of outer circle
  let innerCircle = null;
  if (hasAbsInfo && unionValue < absValInfo.value * 0.9) {
    innerCircle = countryInfo;
  } else if (sizeBase === 'history' && unionValue < countryBaseValue * 0.9) {
    innerCircle = {
      head: `MAP in ${formatMonth(month)}`,
      value: formatNumber(unionValue),
    };
  }

  return {outerCircle, innerCircle};
};

const getVennDiagram = (apps, month, data, sizeBase, circleOrder, wrapper, absValInfo) => {
  const appCombos = getAppCombos(apps);
  const monthData = data.by_month[month];
  const {baseText, basePop, baseType} = getSizeBaseInfo(sizeBase);

  // get size base valuse - each country's base value
  // and calibarated/absolute base value
  const historyUnionData = getHistoryUnion(sizeBase, data, apps);

  const {unionValue, countryBaseValue, baseValue, isCalibrated} =
      getBaseValue(appCombos, monthData, historyUnionData.value, basePop,
        data, sizeBase, absValInfo);

  // get size of circles of apps with width, height, and padding
  const {sets, appCircles, appLabels, totalLabel} = getAppCircles(
    unionValue, countryBaseValue, baseValue, isCalibrated, wrapper,
    appCombos, data.by_month, month, circleOrder,
  );

  // get text center for mouse-over label
  const textCenters = venn.computeTextCentres(appCircles, sets);

  // get path information of all permutations including tooltip
  const pathes = [];
  for (let combo of appCombos) {
    const sets = combo.split('-');
    const path = venn.intersectionAreaPath(sets.map(set => appCircles[set]));

    //get tooltip data
    const totalVal = monthData[combo];
    const onlyVal = sets.length === 3 ? null : monthData['only:' + combo];
    const relVal = baseType === 'rel' ? monthData[basePop] : null;
    const tooltip = {
      content: {sets, totalVal, onlyVal, relVal},
      top: textCenters[sets].y.toFixed(1) + 'px',
      left: textCenters[sets].x.toFixed(1) + 'px',
    };
    pathes.push({set: combo, path, tooltip});
  }

  // outline circles
  let {outerCircle, innerCircle} = getOutlineTooltips(
    unionValue, countryBaseValue, historyUnionData, monthData[basePop],
    apps, month, sizeBase,
    data.country, absValInfo
  );

  const circle = {cx: wrapper / 2, cy: wrapper / 2};
  const outer = {
    ...circle, name: 'outer', r: wrapper / 2,
    tooltip: sizeBase !== 'fit' ? outerCircle : null,
  };
  const circles = [outer];

  const innerRBase = sizeBase === 'history' ?
    unionValue :
    Math.max(countryBaseValue, unionValue);
  const inner = innerCircle != null ?
    {
      ...circle, name: 'inner',
      r: Math.sqrt(wrapper * wrapper * innerRBase / 4 / baseValue),
      tooltip: innerCircle,
    } :
    null;
  if (innerCircle != null) {
    circles.push(inner);
  }

  return {circles, pathes, appLabels, totalLabel, isCalibrated, base: baseText, countryName: data.name};
};

const getVennDiagramsAnimated = (apps, data, sizeBase, circleOrder, wrapper, absValInfo) => {

  const historyUnionData = getHistoryUnion(sizeBase, data, apps);

  const circlesByMonth = allMonths.map(m => {
    const appCombos = getAppCombos(apps);
    const monthData = data.by_month[m.value];
    const {basePop} = getSizeBaseInfo(sizeBase);

    const {unionValue, countryBaseValue, baseValue, isCalibrated} =
        getBaseValue(appCombos, monthData, historyUnionData.value, basePop,
          data, sizeBase, absValInfo);

    // get size of circles of apps with width, height, and padding
    const {appCircles, appLabels, totalLabel} = getAppCircles(
      unionValue, countryBaseValue, baseValue, isCalibrated, wrapper,
      appCombos, data.by_month, m.value, circleOrder,
    );

    return {appCircles, appLabels, totalLabel, isCalibrated};
  });

  // get array of monthly data, then by app
  const circlesByApp = {};
  for (let monthDataByApp of circlesByMonth.map(m => m.appCircles)) {
    for (let app in monthDataByApp) {
      if (circlesByApp[app] != null) {
        circlesByApp[app].push(monthDataByApp[app]);
      } else {
        circlesByApp[app] = [monthDataByApp[app]];
      }
    }
  };

  const isCalibrated = circlesByMonth.map(c => c.isCalibrated);
  const appLabels = circlesByMonth.map(c => c.appLabels);
  const totalLabel = circlesByMonth.map(c => c.totalLabel);

  return {circlesByApp, isCalibrated, appLabels, totalLabel};
};

export {getVennDiagram, getVennDiagramsAnimated};
