import React, { Component } from 'react';
import * as d3 from 'd3';
import Tooltip from './Tooltip';
import OutlineTooltip from './OutlineTooltip';
import AppLabels from './AppLabels';
import TotalLabel from './TotalLabel';

class Country extends Component {

  state = {tooltip: null, outlineTooltip: null};

  toggleCalibration(isHide) {
    d3.select(`.js-${this.props.country}-calibration`).classed('hide', isHide);
  }

  handleMouseOver(country, order, set, sets, tooltip) {
    // move the hovered path right below the next-combination level path
    // if the horvered path is moved to the very front, it is hard to revert
    // this way, the position does not have to be reverted.
    const target = document.getElementById(`path-${country}-${order}`);
    const parent = target.parentNode;
    for (let i = order + 1; i < parent.children.length - 1; i++) {
      const nextSibling = document.getElementById(`path-${country}-${i}`);
      // find the next level path
      if (set.slice('-').length < d3.select(nextSibling).attr('set').slice('-').length) {
        parent.insertBefore(target, nextSibling);
        break;
      }
    }

    // style change
    // foucus the overlaid path & defocus the rest
    for (let appCombo of sets) {
      d3.select(`.js-${country}-${appCombo}`)
        .classed('dim', appCombo !== set);
    }
    //show the outline stroke and highlight
    d3.select(`.js-${country}-${set}`).classed('undim', true);

    // show tooltip
    this.setState({tooltip});
  }

  handleMouseOut(country, tooltipType) {
    // reset all highlighted pathes and tooltip
    d3.selectAll(`.js-${country}`)
      .classed('dim', false)
      .classed('undim', false);
    this.setState({[tooltipType]: null});
  }

  handleOutlineMouseOver(country, outlineTooltip) {
    d3.selectAll(`.js-${country}`).classed('dim', true);
    this.setState({outlineTooltip});
  }

  render() {
    const {
      country, wrapperSize,
      isSizeBaseOn, isCountryBaseOn,
      labelLevel, labelValue, labelType,
      circles, appLabels, totalLabel, pathes, isCalibrated, base, countryName,
    } = this.props;

    const {tooltip, outlineTooltip} = this.state;

    return (<div className="Country">
      {isCalibrated && (isSizeBaseOn || isCountryBaseOn) &&
      <div className="base-wrapper">
        <div
          className="base-calibrated"
          onMouseOver={() => this.toggleCalibration(false)}
          onMouseOut={() => this.toggleCalibration(true)}
        />
      </div>}
      <svg width={wrapperSize} height={wrapperSize} id={`svg-${country}`}>
        <g className="js-circles">
          {circles.map((circle, i) => <circle
            cx={circle.cx}
            cy={circle.cy}
            r={circle.r}
            key={i}
            className={`circle-${circle.name}${(isSizeBaseOn && i === 0) || (isCountryBaseOn && i === 1) ? '' : ' hide'}`}
            onMouseOver={() => circle.tooltip != null &&
              this.handleOutlineMouseOver(country, circle.tooltip)}
            onMouseOut={() => this.handleMouseOut(country, 'outlineTooltip')}
          />)}
        </g>
        <g className="js-pathes">
          {pathes.map((path, i) => <path
            key={i}
            set={path.set}
            id={`path-${country}-${i}`}
            className={`circle set-${path.set} js-${country} js-${country}-${path.set}`}
            d={path.path}
            onMouseOver={(e) =>
              this.handleMouseOver(country, i, path.set, pathes.map(p => p.set), path.tooltip)
            }
            onMouseOut={() =>
              this.handleMouseOut(country, 'tooltip')
            }
          />)}
        </g>
      </svg>
      {labelLevel === 'apps' && <AppLabels
        labelValue={labelValue}
        labelType={labelType}
        appLabels={appLabels}
      />}
      {labelLevel === 'total' && <TotalLabel
        labelValue={labelValue}
        labelType={labelType}
        totalLabel={totalLabel}
      />}
      <div className={`hide calibration js-${country}-calibration`}>
        {base} of <strong>{countryName}</strong> was estimated smaller than
        {` `}the monthly active people (MAP) on all of family apps.
        {` `}Thus, we <strong>calibrated the {base.toLowerCase()}</strong> as the total MAP.
      </div>
      {tooltip != null && <Tooltip {...tooltip}/>}
      {outlineTooltip != null && <OutlineTooltip {...outlineTooltip}/>}
    </div>);
  }
}

export default Country;
