import React, { Component } from 'react';

class OutlineTooltip extends Component {

  render() {
    const {head, value, month, calibratedTo, estimated, additional} = this.props;

    return (<div className="tooltip outline">
        <div>{head}</div>
        <div>
          <span className={`tooltip-value${(calibratedTo != null || estimated  != null) ? ' calibrated' : ''}`}>
            {value}
          </span>
          {calibratedTo != null && <span className="tooltip-month"> (calibrated to: {calibratedTo})</span>}
          {estimated != null && <span className="tooltip-month"> (estimated: {estimated})</span>}
          {month != null && <span className="tooltip-month"> - {month}</span>}
        </div>
        {additional != null && <div>
          {additional.head} - <strong>{additional.value}</strong>
        </div>}
      </div>);
  }
}

export default OutlineTooltip;
