import data from './data/data-random';

const allCountriesCount = data.countries.length;
const worldData = {
  name: 'World',
  ...data.world,
};

const allCountrySelections = data.countries.map(c => {
    return {label: `${c.name} (${c.country})`, value: c.country};
  }).sort((a, b) => a.label.localeCompare(b.label));

const allRegions = [...new Set(data.countries.map(c => c.region))];
const allContinents = [...new Set(data.countries.map(c => c.continent))];
const allFilters = [
  {value: 'all', label: 'All countries', list: []},
  {value: 'region', label: 'Region', list: allRegions.sort()},
  {value: 'continent', label: 'Continent', list: allContinents.sort()},
  {value: 'country', label: 'Select one by one', list: []}
];

/**** update this part for different apps ****/
const allApps = [
  {value: 'fb', label: 'Liberte'},
  {value: 'ig', label: 'Egalite'},
  {value: 'wa', label: 'Fraternite'}
];
let appObj = {};
let appIds = [];
for (let app of allApps) {
  appObj[app.value] = app.label;
  appIds.push(app.value);
}

// format months
const monthStr = [
  'Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun',
  'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec',
];
const allMonths = data.data_dates.map((d, i) => {
  let year = +d.slice(0, 4);
  const monthNum = +d.slice(5, 7);
  const month = monthStr[monthNum - 1];
  const label = `${month} ${year}`;
  if (i > 0 && monthNum > 1) {
    year = null;
  }
  // value is used as the key of country > by_month data
  return {value: d.slice(0, 7), year, month, label};
});
const latestMonth = allMonths[allMonths.length - 1].value;
const formatMonth = (m) => {
  let year = +m.slice(0, 4);
  const month = +m.slice(5, 7);
  return `${monthStr[month - 1]} ${year}`;
};

const filterCountries = (filter, filterList) => {
  const countries = data.countries.slice();
  return filter === 'all' ? countries :
    countries.filter(c => filterList.indexOf(c[filter]) > -1);
};

// order By countries options are dynamic, depending on the number of selected apps
const getSortCountriesOptions = (apps) => {
  const getRelative = (value, label, options) => {
    const result = [
      {value, label},
      {value: value + '-p', label: label + ' / Internet Population'},
    ];
    return options.concat(result);
  };

  // app combination
  let options = [];
  if (apps.length > 1) {
    const value = 'total';
    const label = (apps.length === 3 ?
      'Total family' :
      apps.map(a => a.toUpperCase()).join(' & ')
    ) + ' MAP';
    options = getRelative(value, label, []);
  }

  // each app
  const appLabels = {
    fb: 'LI',
    ig: 'EG',
    wa: 'FR',
  }
  for (let value of apps) {
    options = getRelative(appLabels[value], appLabels[value].toUpperCase() + ' MAP', options);
  }

  // common options
  options = options.concat([
    {value: 'internet', label: 'Internet population'},
    {value: 'population', label: 'Total population'},
  ]);

  return options;
};

// resize options
const getResizeCirclesOptions = (apps, countryCount) => {
  let options = [
    {value: 'fit', label: 'Fit the base circle'},
    {
      value: 'history',
      label: 'Historically largest ' +
        (apps.length === 3 ?
          'total family' :
          apps.map(a => a.toUpperCase()).join(' & ')
        ) +
        ' MAP' +
        (countryCount > 1 ? ' of each country' : ''),
    },
  ];
  options.push(
    {
      value: 'internet-rel',
      label: 'Internet population' + (countryCount > 1 ? ' of each country' : ''),
    },
    {
      value: 'population-rel',
      label: 'Total population' + (countryCount > 1 ? ' of each country' : ''),
    },
  );

  // add largest country based option at the end
  if (countryCount > 1) {
    options.push(
      {
        value: 'internet-abs',
        label: 'Internet population of the largest country',
      },
      {
        value: 'population-abs',
        label: 'Total population of the largest country',
      }
    );
  }
  return options;
};

// order cirlces by
// make app combination
const permutator = (inputArr) => {
  let result = [];
  const permute = (arr, m = []) => {
    if (arr.length === 0) {
      result.push(m)
    } else {
      for (let i = 0; i < arr.length; i++) {
        let curr = arr.slice();
        let next = curr.splice(i, 1);
        permute(curr.slice(), m.concat(next))
     }
   }
 }
 permute(inputArr)
 return result;
}

// circle order change
const getOrderCirclesOptions = (apps) => {
  let options = [{value: 'map', label: 'By MAP'}];
  const appPermuations = permutator(apps);
  for (let p of appPermuations) {
    options.push({
      value: p.join('-'), label: p.map(item => appObj[item]).join(', ')
    });
  }
  return options;
};

// circle label options
const getShowLabelsOptions = (apps) => {
  let options = [{value: 'none', label: 'None'}];

  const label = apps.length === 3 ?
    'Total Family' :
    apps.map(a => a.toUpperCase()).join(' & ');

  options = options.concat([
    {value: 'total-topline', label: label + ' Topline'},
    {value: 'total-growth', label: label + ' Growth'},
  ]);

  if (apps.length > 1) {
    options = options.concat([
      {value: 'apps-topline', label: 'App-level Topline'},
      {value: 'apps-growth', label: 'App-level Growth'},
    ]);
  }

  return options;
};

const getAppCombos = (apps) => {
  const sorted = apps.slice(0).sort();
  let result = sorted;
  if (apps.length === 2) {
    result.push(sorted.join('-'));
  } else if (apps.length === 3) {
    result = sorted.concat(
      [appIds[0] + '-' + appIds[1], appIds[0] + '-' + appIds[2],
       appIds[1] + '-' + appIds[2], appIds.join('-')]
    );
  }
  return result;
};
const allAppCombos = getAppCombos(appIds);

const getFamilyMAP = (appCombos, monthData) => {
  // get union value of selected apps
  let unionValue = 0;
  for (let combo of appCombos) {
    // for two apps overlap - sustract, otherwise add the value
    unionValue += monthData[combo] * (combo.length === 5 ? -1 : 1);
  }
  return unionValue;
};

const orderCountries = (data, month, orderBy, orderDir) => {
  let ordered = data.slice(0);
  // internet population or total population
  if (orderBy === 'internet' || orderBy === 'population') {
    ordered.sort((a, b) => b.by_month[month][orderBy] - a.by_month[month][orderBy]);
  } else {
    // MAP-based
    const splitted = orderBy.split('-');
    const app = splitted[0];
    const base = splitted[1];
    const getMAP = (d) => {
      let map = null;
      if (app === 'total') {
        map = getFamilyMAP(allAppCombos, d.by_month[month]);
      } else if (app.indexOf(':') > -1) {
        map = getFamilyMAP(getAppCombos(app.split(':')), d.by_month[month]);
      } else {
        map = d.by_month[month][app];
      }
      return map;
    };
    ordered.sort((a, b) =>
      // relative to total interet population
      getMAP(b) / (base != null ? b.by_month[month].internet : 1) -
      getMAP(a) / (base != null ? a.by_month[month].internet : 1)
    );
  }
  return orderDir === 'asc' ? reverseList(ordered) : ordered;
};

const getCalibratedAbsBase = (largestCountry, sizeBase) => {
  const familyMAP = getFamilyMAP(allAppCombos, largestCountry);
  return {
    isCalibrated: familyMAP > largestCountry[sizeBase],
    value: Math.max(largestCountry[sizeBase], familyMAP)
  };
};

const getLargestCountryInfo = (countries, selectedTime, sizeBase) => {
  const orderedList = orderCountries(countries, selectedTime, sizeBase, 'desc');
  const largestCountry = orderedList[0];
  const {code, name} = largestCountry;
  return {
    code, name,
    ...getCalibratedAbsBase(largestCountry.data[selectedTime], sizeBase),
  };
};

const formatNumber = (num) => {
  const formats = [
    {log: 9, abbr: 'B', decimals: 2},
    {log: 6, abbr: 'M'},
    {log: 3, abbr: 'K'},
  ];
  let formatted = num;
  for (let f of formats) {
    if (Math.log10(Math.abs(num)) > f.log) {
      formatted = (num / Math.pow(10, f.log)).toFixed(f.decimals || 1).replace(/\.0$/, '') + f.abbr;
      break;
    }
  }
  return formatted;
};

const getPercentNumber = (num, baseNum) => {
  const ratio = num / baseNum;
  const percent = (ratio > 1 ? '>' : '') + (Math.min(ratio, 1) * 100).toFixed(1) + '%';
  return percent;
};

const getUpdatedArray = (item, state, preventEmpty) => {
  const idx = state.indexOf(item);
  const dupState = state.slice(0);
  if (idx > -1 && (!preventEmpty || dupState.length > 1)) {
    dupState.splice(idx, 1);
  } else if (idx === -1) {
    dupState.push(item);
  }
  return dupState;
};

const reverseList = (list) => {
  const dup = list.slice(0);
  return dup.reverse();
};

export {
  allCountriesCount,
  worldData,
  allCountrySelections,
  allFilters,
  allApps,
  allMonths,
  latestMonth,
  formatMonth,
  filterCountries,
  getSortCountriesOptions,
  getResizeCirclesOptions,
  getOrderCirclesOptions,
  getShowLabelsOptions,
  getAppCombos,
  getFamilyMAP,
  orderCountries,
  getLargestCountryInfo,
  getUpdatedArray,
  formatNumber,
  getPercentNumber,
  reverseList,
};
