import React, { Component } from 'react';

class AppLabels extends Component {

  render() {
    const {labelValue, labelType, appLabels} = this.props;

    return (
      <div className="labels">
        {appLabels.map((app, i) => <div
          key={i}
          className={`number ${app.app} ${appLabels.length === 2 ? 'even-' : 'odd-'}${app.index}
            ${labelValue === 'growth' && app.growth != null ? ` arrow ${app.growth.arrow[labelType]}` : ''}`}
          style={{...app.pos}}>
          {app[labelValue] != null ? app[labelValue][labelType] : 'N/A'}
        </div>)}
      </div>);
  }
}

export default AppLabels;
