import React, { Component } from 'react';
import Main from './Main';
import {allApps, allCountriesCount} from './processor';
import './App.css';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      // whether the user has confirmed the disclaimer or not
      hasSession: null,
      // the disclaimer checked or not
      checked: false,
    }
  }

  componentDidMount() {
    this.setState({hasSession: localStorage.getItem('confirmed') === 'confirmed'});
  }

  checkDisclaimer() {
    this.setState(currentState => {
      return {checked: !currentState.checked};
    });
  }

  confirmWarning() {
    if (!this.state.checked) {
      return;
    }
    localStorage.setItem('confirmed', 'confirmed');
    this.setState({hasSession: true})
  }

  render() {
    const {hasSession, checked} = this.state;

    return (
      <div>
        <header>
          <div className="title">Overlap of the three</div>
          <div className="desc">
            Monthly Active People of
            {allApps.map((app, i) => <span key={i}>
              <span className={app.value}> {app.label}</span>
              {i < allApps.length - 1 ? ', ' : ' '}
            </span>)}in Top {allCountriesCount} countries
          </div>
        </header>
        <Main/>
        <footer>
          <p>This is a prototype with a fake dataset. Do not reference anything here.</p>
        </footer>
        {!hasSession && <div className="popup">
          <div className="popup-message">
            <div className="warning-icon"/>
            <div>
              <p>
              This is not an official product of any companies.
              All the data used in this page are NOT real and randomly generated purely for prototype purpose.
              </p>
              <p>
                <input
                  type="checkbox"
                  checked={checked}
                  onChange={() => this.checkDisclaimer()}
                /> I understand this disclaimer and agree to use this webpage only for non mission critical work or research.
              </p>
            </div>
            <div className="confirm-wrapper">
              <span className="confirm-botton" onClick={() => this.confirmWarning()}>Do not open this again</span>
            </div>
          </div>
        </div>}
      </div>
    );
  }
}

export default App;
