import React, { Component } from 'react';

class TotalLabel extends Component {

  render() {
    const {labelValue, labelType, totalLabel} = this.props;

    return (
      <div className="labels">
        <div
          className={`number family ${labelValue === 'growth' && totalLabel.growth != null ? ' arrow' : ''}
            ${totalLabel.growth != null ? ` ${totalLabel.growth.arrow[labelType]}`: ''}`}>
          {totalLabel[labelValue] != null ? totalLabel[labelValue][labelType] : 'N/A'}
        </div>
      </div>);
  }
}

export default TotalLabel;
