import React, { Component } from 'react';
import VisualizationControl from './VisualizationControl';
import {
  allCountrySelections,
  allFilters,
  allApps,
  allMonths,
  latestMonth,
  filterCountries,
  getUpdatedArray,
} from './processor';
import './Main.css';

class Main extends Component {

  constructor(props) {
    super(props);
    // default key countries
    const filter = allFilters[3].value;
    const filterList = ['US', 'IN', 'BR', 'JP', 'RU', 'ID', 'MX'];
    const apps = allApps.map(d => d.value);
    const month = latestMonth;
    // in Main, states are for country and app selection
    this.state = {
      isCountryOpen: false,
      showWorld: true,
      // region, continent, or select menu
      filter,
      // filter items in region or continent
      filterList,
      apps,
      // month is passed to a child component VisualizationControl
      // because the change of month triggers the change of visuals
      // although it is not updated in the component VisualizationControl
      month,
      countries: filterCountries(filter, filterList),
      // animation started or paused
      isAnimationStarted: false,
      // when there are more than 7 countries and/or largest-country based sizing,
      // animation is not allowed/possible
      isAnimationVoid: false,
      // when animated, index of the currently displayed month
      animatedMonthIndex: -1,
      isUpdating: false,
    };
  }

  // animatino setting,
  ANIMATION_SPEED = 1; // in second
  MAX_ANIMATION_COUNTRY = 7;

  getSnapshotBeforeUpdate(prevProps, prevState) {
    if (prevState.filter !== this.state.filter ||
      prevState.filterList !== this.state.filterList) {
      return true;
    }
    return null;
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    // since filtering countries takes a while, treat it asyncronously
    if (snapshot) {
      this.setState({isUpdating: true});
      setTimeout(() => {
        const {filter, filterList} = this.state;
        const countries = filterCountries(filter, filterList);
        const isAnimationVoid = countries.length > this.MAX_ANIMATION_COUNTRY;
        this.setState({countries, isAnimationVoid, isUpdating: false});
      });
    }
  }

  // for boolean states - isCountryOpen, showWorld
  toggleSettingPanel(stateName) {
    this.setState(currentState => {
      const currentValue = currentState[stateName];
      return {[stateName]: !currentValue};
    });
  }

  updateFilters(value, isList) {
    this.setState(currentState => {
      const filter = isList ? currentState.filter : value;
      const filterList = isList ? getUpdatedArray(value, currentState.filterList) : [];
      return {filter, filterList};
    });
  }

  updateApps(value) {
    this.setState(currentState => {
      return {
        apps:getUpdatedArray(value, currentState.apps, true)
      };
    });
  }

  updateMonth(month, index) {
    this.setState({
      month,
      animatedMonthIndex: month === latestMonth ? -1 : index
    });
  }

  tick() {
    let {animatedMonthIndex, isAnimationStarted} = this.state;
    if (animatedMonthIndex < allMonths.length - 1) {
      animatedMonthIndex =  animatedMonthIndex + 1;
    } else if (animatedMonthIndex === allMonths.length - 1){
      animatedMonthIndex = -1;
      isAnimationStarted = false;
      clearInterval(this.interval);
    }
    this.setState({animatedMonthIndex, isAnimationStarted});
  }

  toggleAnimation(isAnimationVoid) {
    if (isAnimationVoid) {
      return;
    }
    this.setState(currentState => {
      let {isAnimationStarted, animatedMonthIndex, month} = currentState;
      if (!isAnimationStarted) {
        this.interval = setInterval(() => this.tick(), this.ANIMATION_SPEED * 1000);
      } else {
        clearInterval(this.interval);
        month = allMonths[animatedMonthIndex].value;
      }
      return {
        isAnimationStarted: !isAnimationStarted,
        animatedMonthIndex,
        month,
      };
    });
  }

  animationVoid(isVoid) {
    this.setState({
      isAnimationVoid: isVoid ||
        this.state.countries.length > this.MAX_ANIMATION_COUNTRY
      }
    );
  }

  render() {
    const {
      showWorld, countries, isCountryOpen,
      filter, filterList, apps, month,
      isAnimationStarted, animatedMonthIndex, isAnimationVoid, isUpdating,
    } = this.state;

    const CountryTitle = (<div className="setting-title">
        {showWorld && 'World + '}
        {countries.length} {countries.length === 1 ? 'country' : 'countries'}
        {/* when specific countries are selected */}
        {filter === 'country' && <span className="setting-title-sub">:
          {filterList.map((d, i) => <span
            key={d}
            className={`country-id${isAnimationStarted ? ' disabled' : ''}`}
            onClick={() => this.updateFilters(d, true)}
          >{d}
          </span>)}
        </span>}
        {!isAnimationStarted && <span
          className="open"
          onClick={() => this.toggleSettingPanel('isCountryOpen')}
         >Select countries</span>}
      </div>);

    const CountrySelection = (<div className="setting-content countries">
        <div className="close" onClick={() => this.toggleSettingPanel('isCountryOpen')} />
          <div>
            <input
              type="checkbox"
              name="world"
              value={showWorld}
              checked={showWorld}
              onChange={() => this.toggleSettingPanel('showWorld')}
            /> Show World
          </div>
        {allFilters.map(f => <div key={f.value}>
          <input
            type="radio"
            name="filter"
            value={f.value}
            checked={filter === f.value}
            onChange={(e) => this.updateFilters(e.target.value)}
          /> {f.label}
          {f.list.map(l => <span key={l} className={`filter-list${filter !== f.value ? ' disabled' : ''}`}>
            <input
              type="checkbox"
              value={l}
              checked={filterList.indexOf(l) > -1}
              disabled={filter !== f.value}
              onChange={(e) => this.updateFilters(e.target.value, true)}
            /> {l}
          </span>)}
          {f.value === 'country' && <select
            className={`filter-list${filter !== f.value ? ' disabled' : ''}`}
            onChange={(e) => this.updateFilters(e.target.value, true)}>
            <option>Select a country</option>
            {allCountrySelections.map(c => <option
              key={c.value}
              value={c.value}
              disabled={filter !== f.value}
              >{c.label}</option>)}
          </select>}
        </div>)}
      </div>);

    const AppSelection = (<div className="setting-content apps">
      {allApps.map(a => <div key={a.value} className="family-list">
        <input
          type="checkbox"
          value={a.value}
          checked={apps.indexOf(a.value) > -1}
          onChange={(e) => this.updateApps(e.target.value)}
          />
        {` `}<span className={a.value}>
          {a.label}{a.notes && <span> ({a.notes})</span>}
        </span>
      </div>)}
    </div>);

    const MonthSelection = (<div className="setting-content">
       <div className="animation">
        <div
          className={`start${isAnimationVoid ? ' void' : ''}${isAnimationStarted ? ' pause': ''}`}
          onClick={() => this.toggleAnimation(isAnimationVoid)}
          >
          {isAnimationVoid ?
            `Animation is not available: choose ${
              countries.length > this.MAX_ANIMATION_COUNTRY ?
              `upto ${this.MAX_ANIMATION_COUNTRY + 1} countries` :
              'relative circle size base'}` :
            (isAnimationStarted ? 'Pause animation' : 'Play animation')
          }
        </div>
      </div>
       <div className={`month${isAnimationStarted ? ' disabled' : ''}`}>
        {allMonths.map((m, i) =>
          <div
              key={i}
              className="setting-month"
              style={{minWidth: `${100 / allMonths.length}%`}}
            >
            <div className="month-input">
              <input
                type="radio"
                name="month"
                value={m.value}
                checked={(isAnimationStarted ?
                  allMonths[Math.max(animatedMonthIndex, 0)].value :
                  month) === m.value}
                disabled={isAnimationStarted}
                onChange={(e) => this.updateMonth(e.target.value, i)}
              />
            </div>
            <div className="month-text">{m.month}</div>
            {m.year != null && <div className="year-text">{m.year}</div>}
          </div>)}
      </div>
    </div>);

   return (<div>
      <div className="setting">
        <div className="setting-countries">
          {CountryTitle}
          {(isCountryOpen && !isAnimationStarted) && CountrySelection}
        </div>
        <div className="setting-property">
          {AppSelection}
          {MonthSelection}
        </div>
      </div>
      {countries.length + (showWorld ? 1 : 0) > 0 &&
        <VisualizationControl
          showWorld={showWorld}
          countries={countries}
          apps={apps.sort()}
          month={month}
          isAnimationStarted={isAnimationStarted}
          animatedMonthIndex={animatedMonthIndex}
          animationVoid={(isVoid) => this.animationVoid(isVoid)}
          isUpdating={isUpdating}
        />}
    </div>);
  }
}

export default Main;
